@import "variables.scss";

body {
  background-color: $background-color;
}

.navigateSectionClotch {
  display: flex;
  justify-content: center;
  gap: 2vw;
  font-size: 2.5vw;
}

.linkNavigate {
  transition: 1s;
  color: $primary-color;
  text-decoration: none;

  &:hover {
    transition: 1s;
    color: $secondary-color;
  }
}

.menClotch {
  height: 100vw;
  background-color: $primary-color;
}
